    event.preventDefault()
    if (edgeMenu.value) {
        menuTargetEdges.value = params.summarized ? params.edges : [params.edge]
        if (params.edge) {
            edges[params.edge].name = params.edge
        }
        showContextMenu(edgeMenu.value, event)
    }
}

